body {
    background-color: #bcc0c3;
}

/* .searchPage {
    background-color: #1e1e1e;
} */
a{
    color: #1e1e1e;
}

.searchPage__header {
    display: flex;
    /* z-index: 1000; */
 
    position: absolute;
    align-items: flex-start;
    padding: 30px;
    
    /* border-bottom: 1px solid #1e1e1e; */
}

.searchPage__headerBody {
    margin-top: 0.1vw;
    margin-left: -25%;
}

/* .searchPage__bar {
    margin-left: 12vw;
} */
.searchPage__header a:hover{
    background-color: transparent;
}

.searchPage__logo {
    object-fit: fit;
    height: 10vw;
    margin-top: 1vw;
    margin-left: 3%;
    
    /* box-shadow: #1e1e1e; */
}
.searchPage__logo:hover{
    background-color: transparent;
}

.searchPage__result {
   
    margin: 40px 0;
    /* background-color: #1e1e1e; */
}

.searchPage__resultCount {
    color: #1e1e1e;
    font-size: 18px;
    font-family: "Gill Sans", sans-serif;
}
.searchOutput{
    
    position: absolute;
    top:25%;
/* background-color: #1e1e1e; */
}
.searchPage__results {

    max-width: 650px;

    margin-left: 170px;
    margin-bottom: 100px;
    /* background-color: #1e1e1e; */
}

.searchPage__resultImage {
    object-fit: contain;
    height: 20px;
    width: 20px;
    margin-right: 10px;
}
a{
    text-decoration: none;
    transition: cubic-bezier(0.39, 0.575, 0.565, 1);
}
a:hover{
    text-decoration: none;
    background-color: #1e1e1e;
    color: white;
    transition: cubic-bezier(0.39, 0.575, 0.565, 1);
}
  /* Add rounded corners to the top left and the top right corner of the image */

  .searchPage_resultCard{
    background-color: rgba(152, 128, 128, 0);
    width: 140%;
    border-radius: 9px;
    padding: 10px 20px;
    margin-bottom: 16px;
    transition: cubic-bezier(0.39, 0.575, 0.565, 1);
    
  }

  .searchPage_resultCard:hover {
    background-color: #3f3d3d;
    transition: cubic-bezier(0.39, 0.575, 0.565, 1);
  }
